import React from "react";

const Modal = (props) => {
  const closeModal = () => {
    props.setShowModal(false);
  };

  return (
    <>
      {props.showModal ? (
        <div id="overlay">
          <div id="inneroverlay">
            <div id="content">
              <p>モーダルウィンドウ</p>
              <p>
                <button className="closebutton" onClick={closeModal}>
                  CLOSE
                </button>
              </p>
            </div>
          </div>
        </div>
      ) : (
        <></>
      )}
    </>
  );
};

export default Modal;
